







































import { defineComponent } from '@vue/composition-api'

export default defineComponent({
  name: 'ToolsClockFields',
  props: {
    hours: {
      type: [String, Number],
      required: true
    },
    minutes: {
      type: [String, Number],
      required: true
    },
    seconds: {
      type: [String, Number],
      required: true
    },
    disabled: {
      type: Boolean
    }
  },
  setup() {
    const padValue = (value: number | string) => {
      return value < 10 ? `0${value}` : value
    }
    return {
      padValue
    }
  }
})
