

















































import {
  defineComponent,
  reactive,
  ref,
  watch,
  onUnmounted
} from '@vue/composition-api'
import ToolsClockFields from './ToolsClockFields.vue'
import { vibrate } from '../use/common'

export default defineComponent({
  name: 'ToolsTimerDialog',
  components: {
    ToolsClockFields
  },
  setup() {
    const tabs = reactive(['Timer', 'Countdown'])
    const currentTab = ref('tab-0')
    const hours = ref(0)
    const minutes = ref(0)
    const seconds = ref(0)
    const pause = ref(true)
    let playInterval: any = null

    const timerMethod = () => {
      seconds.value++
      if (seconds.value > 59) {
        seconds.value = 0
        minutes.value++
        if (minutes.value > 59) {
          minutes.value = 0
          hours.value++
        }
      }
    }

    const countdownMethod = () => {
      seconds.value--
      if (hours.value === 0 && minutes.value === 0 && seconds.value <= 0) {
        stopTimer()
        vibrate()
      } else {
        if (seconds.value < 0) {
          seconds.value = 59
          minutes.value >= 1 ? minutes.value-- : (minutes.value = 0)
          if (minutes.value <= 0 && hours.value > 0) {
            minutes.value = 59
            hours.value >= 1 ? hours.value-- : (hours.value = 0)
          }
        }
      }
    }

    const playPauseTimer = () => {
      pause.value = !pause.value

      if (!pause.value) {
        playInterval = setInterval(() => {
          currentTab.value === 'tab-0' ? timerMethod() : countdownMethod()
        }, 1000)
      } else {
        clearInterval(playInterval)
      }
    }

    const stopTimer = () => {
      pause.value = true
      hours.value = 0
      minutes.value = 0
      seconds.value = 0
      clearInterval(playInterval)
    }

    watch(currentTab, (value, oldValue) => {
      if (value !== oldValue) {
        stopTimer()
      }
    })

    onUnmounted(() => {
      stopTimer()
    })

    return {
      hours,
      minutes,
      seconds,
      pause,
      stopTimer,
      tabs,
      currentTab,
      playPauseTimer
    }
  }
})
